<template>
  <div class="thesis hidden-xs hidden-sm">
    <div class="nullDom"></div>
    <ul class="nav col-xs-12 col-md-12">
      <li class="nav-item" @click="change($event,0)">{{$t('aboutLanguge.navList[0]')}}</li>
      <li class="nav-item" @click="change($event,1)">{{$t('aboutLanguge.navList[1]')}}</li>
      <li class="nav-item" @click="change($event,2)">{{$t('aboutLanguge.navList[2]')}}</li>
      <li class="nav-item" @click="change($event,3)">{{$t('aboutLanguge.navList[3]')}}</li>
      <li class="nav-item" @click="change($event,4)">{{$t('aboutLanguge.navList[4]')}}</li>
      <li class="nav-item" @click="change($event,5)">{{$t('aboutLanguge.navList[5]')}}</li> 
      <li class="nav-item" @click="change($event,6)">{{$t('aboutLanguge.navList[6]')}}</li> 
    </ul>
    <div 
    class="thesis-article" 
    v-for="(item, index) in thesisList[this.value][1][this.page-1]"
    :key="item.id">
      <div class="thesis-list">
        <h2 class="thesis-title">
          <div>
            {{item.title}}
          </div>
          <a :href="item.src" target="_blank" v-if="item.src" class="icon icon-thesis_icon_pdf2">
          </a>
        </h2>
        <p class="thesis-author">
          <span>{{item.time}}</span> | <span>{{item.author}}</span>
        </p>
        <div class="thesis-content" ref="openContent">
          <p class="thesis-content-text" ref="contentText" v-html="item.content"></p>
          <p class="thesis-open" @click="open($event,index)" ref="openText">展开</p>
        </div>
        
      </div>
    </div>
    <ul class="page">
      <li class="triangle" @click="addPage"></li>
      <li @click="changePage(page+1)" v-if="(thesisList[this.value][0][0]/5)>1&&(page+1)<=pageCount">{{page+1}}</li>
      <li @click="changePage(page)" class="currentIndex">{{page}}</li>
      <li class="triangle2" @click="reducePage"></li>
      <li>{{$t('aboutLanguge.Total')}}<span>{{thesisList[this.value][0][0]}}</span>{{$t('aboutLanguge.item')}}</li>
    </ul>
  </div>
</template>

<script>
export default {
  data () {
    return {
      value: 0,
      pageCount:1,
      textList: [],
      textListOld:[],
      isIE: false,
      page: 1,
      index: 1,
    }
  },
  created() {
    this.textListOldZh = JSON.parse(JSON.stringify(this.thesisList))
    this.textList = this.textListOldZh
  },
  computed: {
    thesisList() {
      return this.$t('aboutLanguge.thesisList')
    }
  },
  watch: {
    thesisList() {
      if(this.index<2) {
        this.index++;
        this.textListOldEn = JSON.parse(JSON.stringify(this.thesisList))
      }
      this.textList = []
      if(this.$i18n.locale === 'zh'){
        this.textList = this.textListOldZh;
        this.$refs.openText.forEach((item) => {
        item.innerHTML = '展开'
      })
      }else {
        this.textList = this.textListOldEn
        this.$refs.openText.forEach((item) => {
        item.innerHTML = 'More'
      })
      }
      this.wordlimit(500);
      
    }
  },
  mounted () {
    if(this.$i18n.locale === 'zh'){
       this.$refs.openText.forEach((item) => {
        item.innerHTML = '展开'})
    }else {
      this.$refs.openText.forEach((item) => {
        item.innerHTML = 'More'
      })
    }
    let pageNum = Math.ceil(this.thesisList[this.value][0][0]/5);
    this.pageCount = pageNum;
    // 让所有论文文案的长度在550，超出便省略号
    this.$nextTick(() => {
      this.wordlimit(500);
    })
    // 判断是否是ie浏览器
    // let thesisBgc = document.getElementsByClassName('thesis');
    if(navigator.userAgent.indexOf("MSIE")>-1 || navigator.userAgent.indexOf("Edge")>-1 || navigator.userAgent.indexOf("Trident")>-1 ) {
      this.isIE = true;
    }
    // 初始化 tabBar 的样式
    let liDom = document.getElementsByClassName("nav-item");
    liDom[0].style.color = "rgba(255,255,255,1)";
    liDom[0].style.borderBottom = "4px solid rgba(50,130,255,1)";
  },
  methods: {
    // 超出长度变省略号（初始化）
    wordlimit(wordlength) {
      // const classN = document.getElementsByClassName(className);
      const open = document.getElementsByClassName('thesis-open');
      for(let i = 0; i<this.thesisList[this.value][1][this.page-1].length; i++) {
        const nowLength = this.thesisList[this.value][1][this.page-1][i].content.length;
        if(nowLength > wordlength) {
          this.$refs.contentText[i].innerHTML = this.textList[this.value][1][this.page-1][i].content.substr(0, wordlength) + '...';
          // this.thesisList[this.value][1][this.page-1][i].content = this.textList[this.value][1][this.page-1][i].content.substr(0, wordlength) + '...';
        }else {
          open[i].innerHTML = '';
          this.$refs.contentText[i].innerHTML = this.textList[this.value][1][this.page-1][i].content
          // this.thesisList[this.value][1][this.page-1][i].content = this.textList[this.value][1][this.page-1][i].content
        }
      }
    },
    // 点击收起文案超出变省略号
    openWordlimit(wordlength, index) {
      if(this.thesisList[this.value][1][this.page-1][index].content.length>wordlength) {
        // this.thesisList[this.value][1][this.page-1][index].content = this.thesisList[this.value][1][this.page-1][index].content.substr(0, wordlength) + '...';  
        this.$refs.contentText[index].innerHTML = this.thesisList[this.value][1][this.page-1][index].content.substr(0, wordlength) + '...';
      }else {
        this.$refs.contentText[index].innerHTML = this.thesisList[this.value][1][this.page-1][index].content.substr(0, wordlength);
        // this.thesisList[this.value][1][this.page-1][index].content = this.thesisList[this.value][1][this.page-1][index].content.substr(0, wordlength);
      }
    },
    // 点击展开文案按原长度显示
    closeWordlimit(wordlength, index) {
      // this.thesisList[this.value][1][this.page-1][index].content = this.textList[this.value][1][this.page-1][index].content+`&nbsp;&nbsp;&nbsp;`
      this.$refs.contentText[index].innerHTML = this.textList[this.value][1][this.page-1][index].content+`&nbsp;&nbsp;&nbsp;`
    },
    // 点击展开收起的控制与文案的转变
    open (event,index) {
      if (event.target.innerHTML == '展开') {
        this.closeWordlimit(900, index);
        event.target.innerHTML = '收起';
        return
      }
      if (event.target.innerHTML == '收起') {
        this.openWordlimit(500, index);
        event.target.innerHTML = '展开';
        return
      }
      if (event.target.innerHTML == 'More') {
        this.closeWordlimit(900, index);
        event.target.innerHTML = 'Fold';
        return
      }
      if (event.target.innerHTML == 'Fold') {
        this.openWordlimit(500, index);
        event.target.innerHTML = 'More';
        return
      }
    },
    // 展开收起文案初始
    initText() {
      if(this.$i18n.locale === 'zh'){
          this.$refs.openText.forEach((item) => {
            item.innerHTML = '展开'
          })
      }else {
        this.$refs.openText.forEach((item) => {
          item.innerHTML = 'More'
        })
      } 
    },
    // 切换tabBar
    change (event, val) {
      this.value = val
      this.page = 1;
      let liDom = event.target;
      let parentEl = liDom.parentNode ;
      let childs = parentEl.children ;
      // 切换时初始化tabBar每一项的字体颜色及其下边框
      for ( var i=0; i<childs.length; i++ ) {
        childs[i].style.color = "rgba(172,172,172,1)";
        childs[i].style.borderBottom = "4px solid transparent";
      }
      // 切换至具体某一项改变其颜色和边框
      liDom.style.color = "rgba(255,255,255,1)";
      liDom.style.borderBottom = "4px solid rgba(50,130,255,1)";
      // 数据变化时算出当前数据可以分几页
      let pageNum = Math.ceil(this.thesisList[this.value][0][0]/5);
      this.pageCount = pageNum;
      // 让所有论文文案的长度在550，超出便省略号
      this.$nextTick(() => {
        this.wordlimit(500);
        this.initText()
      })
    },
    // 点击中间数字切换页面
    changePage (page) {
      this.page = page;
      this.$nextTick(() => {
        this.wordlimit(500);
        this.initText()
      })
    },
    // 加页面
    addPage () {
      if (this.page <  this.pageCount) this.page++;
      else return;
      this.$nextTick(() => {
        window.scrollTo(0,0)
        this.wordlimit(500);
        this.initText()
      })
    },
    // 减页面
    reducePage () {
      if (this.page > 1 ) this.page--;
      else return;
      this.$nextTick(() => {
        window.scrollTo(0,0)
        this.wordlimit(500);
        this.initText()
      })
    }
  }
}
</script>

